import React, { memo } from 'react'

const GamePad = props => {
    const { moveUp, moveDown, moveLeft, moveRight } = props

    return (
        <div className="gamepad">
            <div className="group left">
                <i className="fas fa-chevron-up up ico" onClick={moveUp} onTouchEnd={moveUp}></i>
                <i className="fas fa-chevron-left left ico" onClick={moveLeft} onTouchEnd={moveLeft}></i>
                <i className="fas fa-chevron-down down ico" onClick={moveDown} onTouchEnd={moveDown}></i>
            </div>
            
            <div className="group right">
                <i className="fas fa-chevron-up up ico" onClick={moveUp} onTouchEnd={moveUp}></i>
                <i className="fas fa-chevron-right right ico" onClick={moveRight} onTouchEnd={moveRight}></i>
                <i className="fas fa-chevron-down down ico" onClick={moveDown} onTouchEnd={moveDown}></i>
            </div>
        </div>
    )
} 

export default memo(GamePad)